import React, { Component } from 'react';
import './Header.css'
import anchorIcom from '../../images/Anchor-icon.png'
import {Route,NavLink} from 'react-router-dom'
import Portfolio from '../../containers/Portfolio/Portfolio';
import UploadExcel from '../../containers/UploadExcel/UploadExcel';



class Header extends Component {
    state = {  }
    render() { 
        return (
            <div className="header_main">
                <ul className="header_ul">
                    <li className="header_li"><NavLink to="/Portfolio">Portfolio</NavLink></li>
                    {/* <li className="header_li"><NavLink >Nav</NavLink></li>
                    <li className="header_li"><NavLink >Reserch</NavLink></li>
                    <li className="header_li"><NavLink>Movments</NavLink></li> */}
                    <li className="header_li"><NavLink to="/UploadExcel">Upload Excel</NavLink></li>

                    <li className="header_anchorIcon"><img src={anchorIcom} alt="anchorIcom"/></li>
                </ul>
            <Route path="/Portfolio" exact render={()=> <Portfolio></Portfolio>}   />
            <Route path="/UploadExcel" exact render={()=> <UploadExcel></UploadExcel>}   />
            {/* <Route path="/Portfolio" exact render={()=> <Portfolio></Portfolio>}   />
            <Route path="/Portfolio" exact render={()=> <Portfolio></Portfolio>}   />
            <Route path="/Portfolio" exact render={()=> <Portfolio></Portfolio>}   /> */}
            </div>

         );
    }
}
 
export default Header;